.weather {
  max-width: 450px;
  background-color: #bbd7ff;
  border-radius: 10px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.weather__loading {
  justify-content: center;
  align-items: center;
  display: flex;
}

.weather__loading img {
  max-width: 50px;
}

.weather__header {
  display: flex;
}

.weather__main {
  flex: auto;
}

.weather__city {
  margin-bottom: 15px;
  font-size: 40px;
  font-weight: 700;
}

.weather__status {
  margin-bottom: 30px;
  font-size: 20px;
}

.weather__temp {
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 700;
}

.weather__temp:after, .weather__feels-like:after {
  content: "°C";
  margin-left: 5px;
  display: inline-block;
}

